import React, { useState } from 'react';

import './Services.scss';
import Service from './Service/Service';
import { Curier, Envelope } from './../../assets/image';

const Services = props => {
   const [service] = useState({
      contact: {
         heading: 'Написать нам',
         icon: Envelope
      },
      curier: {
         heading: 'Заказ курьера',
         icon: Curier
      },
      // about: {
      //    heading: 'O нас',
      //    icon: ''
      // },
      // advertisement: {
      //    heading: 'Реклама',
      //    icon: ''
      // }
   });

   const allServices = Object.keys(service).map(serv => (
      <Service 
         key={serv}
         heading={service[serv].heading}
         icon={service[serv].icon}
      />
   ));
   return (
      <div className="Container">
         <div className="Services">
            <h3 className="Services__Header">СВЯЗАТЬСЯ С НАМИ</h3>
            <div className="Services-Box">
              {allServices}  
            </div>
         </div> 
      </div>
   );
};

export default Services;