import React, { useEffect, useState } from 'react';

import './CurrencyBuilder.scss';
import Currency from './../../components/Widgets/Currency/Currency';
import axios from 'axios';
// let bnm = require('bnm-api');

const CurrencyBuilder = () => {
   const [currency, setCurrency] = useState({
      showSpinner: true
   });
   useEffect(() => {
      axios.get('https://api.bnm.gov.my/public/exchange-rate/USD')
      .then( res => {
         console.log(res);
      }).catch(err => {
         console.log(err);
      })
   }, []);

   return (
      <>
         <Currency/>
      </>
   );
};

export default CurrencyBuilder;


